<template>
    <div>
        <Button type="info" shape="circle" @click="ScatterChange">Q 值图</Button>
        <Button type="info" shape="circle" @click="BarChange" style="margin-left: 10px">柱状图</Button>
        <keep-alive>
            <component :is="currentView"></component>
        </keep-alive>
    </div>
</template>

<script>
    import TestEcharts from './Testecharts'
    import TestBar from './TestBar'

    export default {
        name: "MyHome",
        components: {
            TestEcharts,
            TestBar
        },
        data() {
            return {
                currentView: 'TestEcharts',
            }
        },
        methods: {
            ScatterChange() {
                this.currentView = 'TestEcharts'
            },
            BarChange() {
                this.currentView = 'TestBar'
            }
        }
    }
</script>

