import Vue from 'vue'
import Router from 'vue-router'
import MyHome from './views/MyHome'
import Testecharts from './views/Testecharts'
import TestBar from './views/TestBar'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: MyHome
    },
    {
      path: '/testecharts',
      component: Testecharts
    },
    {
      path: '/testBar',
      component: TestBar
    }
  ]
})
